.footer{
    color: white;
    background-color: rgb(22, 22, 22);
    padding: 3rem 5%;
}
.footer-container{
    display: flex;
}
.footer-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1rem;
}
.footer-left div{
    display: flex;
    gap: 16px;
}



.footer-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1rem;
}
.footer-right div{
    display: flex;
    gap: 16px;
}
.footer-social .icon{
    color: cadetblue;
    font-size: 24px;
    cursor: pointer;
}
.footer-social .icon:hover{
    color: rgb(44, 112, 114);
}


@media (max-width: 576px){
    .footer-container{
        flex-direction: column;
        gap: 3rem;
    }
}