.contact-form{
    width: 100%;
    padding: 3rem 10%;
    background-color: #000;
    color: white;
    
}
.contact-form form{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 400px;
    margin: 0 auto;
}
.contact-form form input, 
.contact-form form textarea{
    padding: 6px;
    background-color: rgba(255,255,255,0.1);
    border-color: rgba(255,255,255,0.1);
    color: white;
}
.contact-form form button{
    display: block;
    width: 100%;
    margin: 0;
    border: none;
}