*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
html{
    scroll-behavior: smooth;
}
ul li{

    list-style: none;
}
a{
    text-decoration: none;
}
p{
    font-size: 14px;
    line-height: 20px;
}
.btn{
    padding: 11px 16px;
    font-size: 14px;
    font-weight: bold;
    background-color: orange;
    color: black;
    cursor: pointer;
    margin: 8px 10px;
    width: 130px;
    display: inline-block;
}
.btn:hover{
    background-color: #FF8C00;
    color: white;
}
.btn-trans{
    padding: 10px 16px;
    font-size: 14px;
    font-weight: bold;
    background-color: transparent;
    border: 1px solid orange;
    color: white;
    cursor: pointer;
    margin: 8px 10px;
    width: 130px;
    display: inline-block;
}
.btn-trans:hover{
    border: 1px solid #FF8C00;
    color: orange;
}



@media (max-width: 576px){
    .btn{
        padding: 8px 10px;
        font-size: 12px;
        margin: 8px;
        width: 110px;
    }
    .btn-trans{
        padding: 8px 10px;
        font-size: 12px;
        margin: 8px;
        width: 110px;
    }
}