.about-content{
    width: 100%;
    padding: 3rem 10%;
    background-color: #000;
    color: white;
}
.about-content-container{
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    margin-bottom: 2rem;
}

.about-left{
    flex: 1;
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    margin: auto;
    padding: 2rem;
}
.about-left p{
    text-align: justify;
}



.about-right{
    flex: 1;
    position: relative;
    text-align: end;
}
.about-right .image-container img{
    width: 70%;
}
.about-right .image-container .image-1{
    top: 2rem;
    left: 0;
    height: 200px;
    position: absolute;
}
.about-right .image-container .image-2{
    height: 200px;
}


@media (max-width: 576px){
    .about-content-container{
        flex-direction: column;
    }
    .about-left{
        padding: 0px;
    }
    .about-left h2{
        font-size: 20px;
    }
}