.projects-card{
    width: 100%;
    background-color: black;
    color: white;
    padding: 2rem 0;
}
.projects-card h1{
    text-align: center;
    padding-bottom: 1rem;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 1rem 10%;
    gap: 1.5rem;
}


@media (max-width: 576px){
    .projects-card h1{
        font-size: 20px;
        padding-bottom: 8px;
    }
}