.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 10%;
    position: fixed;
    z-index: 1000;
    width: 100%;
}
.header-bg-active{
    background-color: #000;
}
.header .logo{
    color: white;
    font-weight: bold;
}
.nav-menu{
    display: flex;
    align-items: center;
    gap: 24px;
}
.nav-menu a{
    color: white;
    font-size: 16px;
    font-weight: bold;
}
.menu-button{
    display: none;
}

@media (max-width: 768px) {
    .header{
        position: fixed;
        background-color: transparent;
    }
    .header-bg-active{
        background-color: #000;
    }
    .nav-menu{
        flex-direction: column;
        width: 100%;
        height: 100vh;
        text-align: center;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        position: absolute;
        z-index: -1;
        transition: .3s;
        background-color: gray;
    }
    .nav-menu.active{
        left: 0;
        transition: .3s;
    }
    .menu-button{
        display: initial;
        cursor: pointer;
    }
}