.hero{
    width: 100%;
    height: 100%;
}
.hero-bg-image{
    width: 100%;
    height: 100vh;
    position: relative;
}
.hero-bg-image img{
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    object-fit: cover;
}
.hero-bg-image::after{
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: rgba(0,0,0,0.8);
}
.hero-content{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    position: absolute;
    text-align: center;
    color: white;
    width: 100%;
}
.hero-content h1{
    padding: 16px 0;
}
.hero-content .hero-button{
    margin: 8px;
}


@media (max-width: 576px){
    .hero-content h4{
        font-size: 14px;
    }
    .hero-content h1{
        padding: 10px 0;
        font-size: 24px;
    }
    .hero-content .hero-button{
        margin: 4px;
    }
}
