
.project-each-card .card{
    padding: 1rem;
    text-align: center;
    width: 250px;
    flex: 1;
    background-color: rgb(30, 30, 30);
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.project-each-card .card:hover{
    background-color: rgb(55, 55, 55);
}
.project-each-card .card img{
    width: 100%;
    height: 125px;
}
.project-each-card .card .card-btn{
    display: flex;
}
.project-each-card .card .card-btn .btn{
    padding: 5px 8px;
}


@media (max-width:576px){
    .project-each-card .card h3{
        font-size: 16px;
    }
    .project-each-card .card p{
        font-size: 14px;
    }
}