.pricing-card{
    width: 100%;
    background-color: black;
    color: white;
}
.card-container{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 3rem 10%;
    gap: 2rem;
}
.card-container .card{
    border: 1px solid lightgray;
    padding: 1.5rem;
    text-align: center;
    min-width: 300px;
}
.card-container .card:hover{
    background-color: rgb(55, 55, 55);
}
.card-container .card h3{
    font-size: 1.4rem;
}
.card-container .card p{
    padding: 10px 0;
}
.card-container .card .bar{
    border-top: 2px solid whitesmoke;
    width: 30%;
    margin: 1rem auto;
    display: block;
}
.card-container .card .price{
    font-size: 3rem;
}
.card-container .card .btn{
    display: block;
    width: 90%;
}


@media (max-width: 576px){
    .card-container{
        display: flex;
        flex-direction: column;
        padding: 1rem 8px;
        gap: 1rem;
    }
}