.hero2{
    width: 100%;
    height: 80vh;
    background-color: rgba(0,0,0,0.5);
    position: relative;
}
.hero2::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1498050108023-c5249f4df085?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=872&q=80");
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-size: cover;
    background-position: center;
    /* background-size: cover; */
    /* background-position: center top; */
    z-index: -1;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}


@media (max-width: 576px){
    .hero2 h1{
        font-size: 24px;
    }
    .hero2 h4{
        font-size: 16px;
    }
}